import * as axios from 'axios'

export default {
    actions:{
        async  fetchServers(ctx,url) {
            let servers = await axios.get(`http://${url}.ngrok.io/servers`).then(response => {
                return response.data
            })
            ctx.commit('putServers', servers)

        },
        setUrlA(ctx,url){
            ctx.commit('setUrl',url)
        },
        setCurrentServerA(ctx,{id,hostname}){
            ctx.commit('setServerIdM',{id,hostname})
        },
    },
    mutations:{
        putServers(state,servers){
            state.servers = servers
        },
        setServerIdM(state,{id,hostname}){
            state.currentServer = {id,hostname}
        },
        setUrl(state,url){
            state.url = url
        }
    },
    getters:{
        getServers(state){
            return state.servers
        },
        getCurrentServer(state){
            return state.currentServer
        },
        getLoginStatus(state){
            return state.servers[2]  ? "true" : "none"
        },
        getUrl(state){
            return state.url
        }

    },
    state:{
        servers:[],
        currentServer:{},
        url:''
    }
}