<template>
  <div class="container mt-2">
    <div class="row">
      <div class="col-3">Servername</div>
      <div class="col-3">Description </div>
      <div class="col-3">Host</div>
    </div>
    <div v-for="server in getServers" v-bind:key="server.id"  class="row mt-1 mb-1  ">
      <div class="col-3" @click="()=>{setCurrentServerA({id:server.id,hostname:server.hostname})}"><router-link to="/databases">{{server.hostname }}</router-link></div>
      <div class="col-3">{{server.alias}}</div>
      <div class="col-3">{{server.id}}</div>
    </div>
  </div>
</template>

<script>
  import {mapActions, mapGetters} from "vuex";

export default {
  name: 'ServersPage',
  methods:{
    ...mapActions(['setCurrentServerA','fetchServers']),
  },
  computed:{
    ...mapGetters(['getServers','getUrl'])
  },
  created() {
    this.fetchServers(this.getUrl)
  }
}
</script>

