import * as axios from 'axios'

export default {
    actions: {
        async fetchpgBadgerAPI(ctx, {id,url}) {
            let pgBadger = await axios.get(`http://${url}.ngrok.io/server/databases/pgBadger/${id}`).then(response => {
                return response.data
            })
            ctx.commit('fetchpgBadger', pgBadger)
        },
        async putNewpgBadgerAPI(ctx, {firstdate, lastdate, dbid, srvid,url}) {
            let pgBadger = await axios.put(`http://${url}.ngrok.io/PutDataForNewpgBadger`, {
                firstdate,
                lastdate,
                dbid,
                srvid
            }).then(response => {
                return response.data
            })
            ctx.commit('fetchpgBadger', pgBadger)
        },
    },
        mutations: {
            fetchpgBadger(state, pgBadger) {
                state.pgBadger = pgBadger
            }
        },
        getters: {
            getpgBadger(state) {
                return state.pgBadger
            }
        },
        state: {
            pgBadger: {},
        }
    }