import * as axios from 'axios'

export default {
    actions: {
        async fetchDataBasesAPI(ctx, {DBid,url}) {
            let DBs = await axios.get(`http://${url}.ngrok.io/servers/databases/${DBid}`).then(response => {
                return response.data
            })
            ctx.commit('fetchDataBases', DBs)
        }
    },
    mutations: {
        fetchDataBases(state, DBs) {
            state.DBs = DBs
        }
    },
    getters: {
        getDataBases(state) {
            return state.DBs
        }
    },
    state: {
        DBs: []

    }
}