<template>
    <div class="container">
        <div class="container">
            <p class="text-center display-4">Databases on server:{{getCurrentServer.hostname}}</p>
            <div class="row" v-for="(dbs,index) in getDataBases " :key="index">
            <div class="col-3">{{dbs.datname}}</div>
            <div class="col-3">{{dbs.oid}}</div>
            <div class="col-3"></div>
        </div>
        </div>
        <div class="container mt-5">
            <p class="text-center display-4" >PgBadgerReports</p>
            <div class="row mt-4 mb-4">
                <div class="col">
                    <select v-on:change="(e)=>{setdatname(e.target.value)}" >
                        <option value="all" selected>All databases</option>
                        <option  v-for="(dbs,index) in getDataBases" :value="dbs.oid" :key="index">{{dbs.datname}}</option>
                    </select>
                </div>
                    <div class="col">From<datetime type="datetime" format="yyyy-MM-dd HH:mm:ss"  v-model="firstdate"/></div>
                    <div class="col">To<datetime type="datetime" format="yyyy-MM-dd HH:mm:ss"  v-model="lastdate"/></div>
                    <div class="col"><button v-on:click="sendRequestForNewPgBadger">Add new report request</button></div>
                </div>
            </div>
            <div class="row">
                <div class="col-3">Server</div>
                <div class="col-3">Database</div>
                <div class="col-3">TimeFrame</div>
                <div class="col-2">Status</div>
            </div>
            <PGBadger  v-for="(pgBadger,index) in getpgBadger"
                       :key="index"
                       :alias="pgBadger.alias"
                       :status="pgBadger.status"
                       :datname="pgBadger.datname"
                       :begin="pgBadger.Begin_time"
                       :end="pgBadger.End_time"
                       :id="pgBadger.id"
                        :filename="pgBadger.filename"/>
        </div>
</template>
<script>
    import {mapActions, mapGetters} from "vuex";
    import PGBadger from "@/components/PGBadger";
    import {Datetime} from 'vue-datetime'
    export default {
        name:'DBsPage',
        components:{
            "PGBadger":PGBadger,
            "datetime":Datetime
        },
        data:function(){
            return{
                status:"ready",
                firstdate:"",
                lastdate:"",
                dbid:"all"
            }
        },
        methods:{
            ...mapActions(['fetchDataBasesAPI','fetchpgBadgerAPI','putNewpgBadgerAPI']),
            setdatname:function(n){
                this.dbid = n
            },
            sendRequestForNewPgBadger:function(){
                this.putNewpgBadgerAPI({firstdate:this.firstdate , lastdate:this.lastdate , dbid:this.dbid ,srvid: this.getCurrentServer.id,url:this.getUrl})
            }
        },

        computed:{
            ...mapGetters(['getDataBases','getCurrentServer','getpgBadger','getUrl']),
        },
        async mounted() {
            setTimeout(()=>{
                console.log(this.getUrl)
                this.fetchDataBasesAPI({DBid:this.getCurrentServer.id,url:this.getUrl});
                this.fetchpgBadgerAPI({id:this.getCurrentServer.id,url:this.getUrl})
            },100)

        }
    }
</script>
